<script>
import Vue from 'vue'
import Bus from '@/common/js/bus.js'
import { coin, heyuesell, heyuebuy } from '@/api/user'
import { objKeySort, sgin } from '@/utils/auth'
import { DropdownMenu, DropdownItem, Icon, Toast } from 'vant'
Vue.use(DropdownMenu).use(DropdownItem).use(Icon).use(Toast)
export default {
  components: {},
  data () {
    return {
      value1: 0,
      value2: 'a',
      number: '',
      paypass: '',
      option1: [
        { text: 'BTC/USDT', value: 0 }
      ],
      option: [],
      parameter: {
        market: 'coin',
        page: 1,
        showpage: 10
      },
      isShow: false,
      // 合约提交data
      heyuesellData: {
        u_id: '10', // int用户ID
        type: '看涨', // 类型（看涨、平空）
        market_price: '9229.7500', // 市场价格
        margin: '9322.11', // 保证金
        fee: '300', // 手续费
        multiple: '20', // 倍数
        lots: '1', // 手数
        symbol: 'btc', // 币种
        paypass: '' // 交易密码
      },
      isSelect: 0
      // option2: [
      //   { text: '默认排序', value: 'a' },
      //   { text: '好评排序', value: 'b' },
      //   { text: '销量排序', value: 'c' }
      // ]
    }
  },
  created () {
    this.heyuesellData.u_id = sessionStorage.getItem('u_id') // 合约提交参数
    // this.bibisellData.u_id = sessionStorage.getItem('u_id')
    var data = objKeySort(this.parameter)
    var token = sgin(data)
    this.parameter.token = token
    this.marketList(this.parameter) // 下拉币种
  },
  methods: {
    userChange (value) {
      console.log('type', value)
      var that = this
      for (const [i, v] of that.option.entries()) {
        if (v.value === value) {
          console.log('等于了', i, v, v)
          Bus.$emit('marketPrice', v.current_price_usd)
          Bus.$emit('symbol', v.text)
          // that.heyuesellData.multiple = v.value
        }
      }
    },
    userSub () {
      var that = this
      if (that.heyuesellData.paypass === '') {
        that.$toast({ message: '交易密码不能为空', duration: 1500 })
        return
      }
      if (this.heyuesellData.type === '看涨' || this.heyuesellData.type === '平空') {
        this.heyuesellFun()
      } else if (this.heyuesellData.type === '看跌' || this.heyuesellData.type === '平多') {
        this.heyuebuyFun()
      }
    },
    heyuesellFun () {
      var that = this
      var data = objKeySort(this.heyuesellData)
      var token = sgin(data)
      this.heyuesellData.token = token
      heyuesell(this.heyuesellData).then(res => {
        console.log(res)
        if (res.code === 0) {
          that.$toast({ message: '挂单成功', duration: 1500 })
          that.isShow = false
          this.heyuesellData.paypass = ''
          delete that.heyuesellData.token
        }
      }).catch((err) => {
        that.$toast({ message: err, duration: 1500 })
        that.isShow = false
        this.heyuesellData.paypass = ''
        delete that.heyuesellData.token
      })
    },
    heyuebuyFun () {
      var that = this
      var data = objKeySort(this.heyuesellData)
      var token = sgin(data)
      this.heyuesellData.token = token
      heyuebuy(this.heyuesellData).then(res => {
        console.log(res)
        if (res.code === 0) {
          that.$toast({ message: '挂单成功', duration: 1500 })
          that.isShow = false
          this.heyuesellData.paypass = ''
          delete that.heyuesellData.token
        }
      }).catch((err) => {
        that.$toast({ message: err, duration: 1500 })
        that.isShow = false
        this.heyuesellData.paypass = ''
        delete that.heyuesellData.token
      })
    },
    showBox () {
      this.isShow = false
    },
    toOrder () {
      this.$router.push({
        path: '/personal/order',
        query: {
          types: '合约'
        }
      })
    },
    navclick (type) {
      this.isSelect = type
    },
    marketList (parameter) {
      var that = this
      coin(parameter).then(res => {
        // console.log('res市场', res)
        that.list = res.data
        for (const [i, v] of res.data.entries()) {
          var arr = {
            text: '',
            value: 0,
            current_price_usd: '',
            scale: ''
          }
          if (v.name === 'USDT/USDT') {
            // console.log('等于了')
          } else {
            arr.text = v.name
            arr.value = i
            arr.current_price_usd = v.current_price_usd
            arr.scale = v.scale
            that.option.push(arr)
          }
          // console.log(that.option, 'option')
          that.userCurrency = that.option[0].text
          that.isScale = that.option[0].scale
          that.isAmount = that.option[0].current_price_usd
          that.current_price_usd = that.option[0].current_price_usd
        }
        Bus.$emit('marketPrice', that.option[0].current_price_usd)
        Bus.$emit('symbol', that.option[0].text)
      }).catch((err) => console.log(err))
    }
  },
  mounted () {
    Bus.$on('getTarget', target => {
      this.heyuesellData = target
      if (this.heyuesellData.type === 1) {
        this.heyuesellData.type = '看涨'
      } else if (this.heyuesellData.type === 2) {
        this.heyuesellData.type = '看跌'
      } else if (this.heyuesellData.type === 3) {
        this.heyuesellData.type = '平空'
      } else if (this.heyuesellData.type === 4) {
        this.heyuesellData.type = '平多'
      }
    })
    Bus.$on('showBox', showBox => {
      if (showBox === true) {
        this.isShow = showBox
      }
    })
    // console.log(this.newsList)
    // this.domain = CommonSet.fileUploadUrl
  }
}
</script>
